import React from "react";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import { AppBar, Toolbar, Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import ClothesList from '../../ClothesList/ClothesList'
const useStyles = makeStyles({
  root: {
    flexGrow: 1
  }
});

class NavigationTop extends React.Component {

  handleClick = (category) => {
    return <ClothesList category={'t-shirts'} />
  }
  render() {
    return (
      <div className={useStyles.root}>
        <AppBar position='static'>
          <Toolbar>
            <Box p={2}>
              <Link to="/t-shirts" replace>
                <Button variant='contained' color='secondary' onClick={() => this.handleClick('t-shirts')}>
                  T-Shirts
                </Button>
              </Link>
            </Box>
            <Box p={2}>
              <Link  to="/jeans" replace>
                <Button variant='contained' color='secondary' onClick={() => this.handleClick('jeans')}>
                  Jeans
                </Button>
              </Link>
            </Box>
            <Box p={2}>
              <Link  to="/shoes" replace>
                <Button variant='contained' color='secondary' onClick={() => this.handleClick('t-shirts')}>
                  Shoes
                </Button>
              </Link>
            </Box>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

export default NavigationTop;
